import React, { Component } from 'react';
import { ReactiveBase, ReactiveList,DataSearch, DataController} from '@appbaseio/reactivesearch';
import './App.css';
import planecrop from './img/planecrop.png';


class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      lat: 42,
      lng: -83,
    }
  }
  
  
  componentDidMount() {
    window.navigator.geolocation.getCurrentPosition(position => {
      this.setState({ lat: position.coords.latitude, lng: position.coords.longitude });
    }, err => console.log(err)
    );
  }	


	render() {
		return (
		    <div className="container" style={{"paddingLeft":"10%", "paddingRight":"10%"}}>
		        <div className="row" style={{"marginBottom":"-30px"}}>
		          <div className="col align-self-center">
		            <h1>Get Spirited Away</h1>
		          </div>
		        </div>
		        <div className="row" style={{"paddingBottom":"20px"}}>
		          <div className="col align-self-center" >
		           <img src={planecrop} alt="plane"></img>
		          </div>
	        	</div>
				<ReactiveBase
					app="getspiritedaway_ver6"
					credentials="avat63oQV:3e28e807-5ddc-475d-9b85-7735b0f21794">
					<DataController
						componentId="geoloc"
						dataField='location'
						value={this.state.lng}
						customQuery={(value, props) => ({ geo_distance: { distance:'100mi','location':{'lon':this.state.lng,'lat':this.state.lat}}})}
					/>
					<DataSearch
					  className="btrow"
					  style={{"paddingLeft":"10%", "paddingRight":"10%","marginBottom":"20"}}
					  autosuggest={false}
					  componentId="search"
					  dataField={[
					    'description',
					    'description.autosuggest',
					    'description.keyword'
					  ]}
					  fieldWeights={[
					    1,
					    1,
					    1
					  ]}
					  fuzziness={0}
					  highlightField={[
					    'description'
					  ]}
					  placeholder="put in your ideal vacation"
					/>
					<div className="col">
						<ReactiveList
							componentId="result"
							dataField="price"
							sortBy="asc"
							pagination={true}
							size={10}
							react={{
						    	and: [
						    	'search',
						        'geoloc'
						    	]
							}}
							onData={this.vacaresults}
						
						/>	
					</div>
				</ReactiveBase>
			</div>
		);
	}


	vacaresults(res){
		var price=res.price.toFixed(0);
		var snip=res.description.substring(0,200)+"..."
		var dest=res.dest.split(',')[0]
		dest=dest.split('/')[0]
		return(
				<div className="flex result-content"> 
				<img className="img url-image" src={res.url} alt="GSA" href={res.url}/>
				<div className="details result-header">
				<div className="info1 city-header">{dest}
				<div className="info1 date-header">{res.departmonth}{res.date}</div>
				</div>
				<div className="info2 price-header">${price}</div>
				<div className="result-footer">
				<a href={res.url}>{snip}</a>
				</div>
				</div>
				</div>
			);
	}

}

  

export default App;